'use client';

import { useState } from 'react';
import { toast } from 'sonner';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';

/**
 * Form validation schema using Zod
 * @constant
 * @type {z.ZodObject}
 * @property {z.ZodString} name - 2-50 characters
 * @property {z.ZodString} email - Valid email format, max 100 chars
 * @property {z.ZodString} message - 10-1000 characters
 */
const formSchema = z.object({
    name: z.string()
        .min(2, '–ò–º—è —Å–ª–∏—à–∫–æ–º –∫–æ—Ä–æ—Ç–∫–æ–µ (–º–∏–Ω–∏–º—É–º 2 —Å–∏–º–≤–æ–ª–∞)')
        .max(50, '–ò–º—è —Å–ª–∏—à–∫–æ–º –¥–ª–∏–Ω–Ω–æ–µ (–º–∞–∫—Å–∏–º—É–º 50 —Å–∏–º–≤–æ–ª–æ–≤)'),
    email: z.string()
        .email('–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email')
        .max(100, 'Email —Å–ª–∏—à–∫–æ–º –¥–ª–∏–Ω–Ω—ã–π'),
    message: z.string()
        .min(10, '–°–æ–æ–±—â–µ–Ω–∏–µ —Å–ª–∏—à–∫–æ–º –∫–æ—Ä–æ—Ç–∫–æ–µ (–º–∏–Ω–∏–º—É–º 10 —Å–∏–º–≤–æ–ª–æ–≤)')
        .max(1000, '–°–æ–æ–±—â–µ–Ω–∏–µ —Å–ª–∏—à–∫–æ–º –¥–ª–∏–Ω–Ω–æ–µ (–º–∞–∫—Å–∏–º—É–º 1000 —Å–∏–º–≤–æ–ª–æ–≤)')
});

type FormData = z.infer<typeof formSchema>;

/**
 * Secure contact form component with JWT token validation
 * @component
 * @description Handles form submission with client-side validation,
 * token management, and error handling
 *
 * @example
 * <ContactForm />
 *
 * @returns {JSX.Element} Accessible contact form with validation
 */
export function ContactForm() {
    const [token, setToken] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);

    /**
     * React Hook Form initialization
     * @type {UseFormReturn<FormData>}
     */
    const {
        register,
        handleSubmit,
        reset,
        formState: { errors }
    } = useForm<FormData>({
        resolver: zodResolver(formSchema)
    });

    /**
     * Fetches new JWT token from API
     * @async
     * @function
     * @returns {Promise<string|null>} Token string or null on failure
     */
    async function getToken() {
        try {
            const res = await fetch('/api/get-token');
            if (!res.ok) {
                const errorMessage = res.status === 429
                    ? '–°–ª–∏—à–∫–æ–º –º–Ω–æ–≥–æ –∑–∞–ø—Ä–æ—Å–æ–≤. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.'
                    : '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —Ç–æ–∫–µ–Ω–∞.';
                toast.error(errorMessage);
                return null;
            }
            return (await res.json()).token as string;
        } catch {
            toast.error('–°–µ—Ç–µ–≤–∞—è –æ—à–∏–±–∫–∞. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.');
            return null;
        }
    }

    /**
     * Handles form submission
     * @async
     * @function
     * @param {FormData} data - Validated form data
     */
    const onSubmit = async (data: FormData) => {
        setLoading(true);

        try {
            const currentToken = token || await getToken();
            if (!currentToken) {
                setLoading(false);
                return;
            }

            const res = await fetch('/api/send-message', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${currentToken}`,
                },
                body: JSON.stringify(data),
            });

            if (res.ok) {
                const newToken = res.headers.get('X-New-Token');
                if (newToken) setToken(newToken);

                toast.success('–°–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ!');
                reset();
            } else {
                toast.error('–û—à–∏–±–∫–∞ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Å–æ–æ–±—â–µ–Ω–∏—è. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.');
            }
        } catch {
            toast.error('–°–µ—Ç–µ–≤–∞—è –æ—à–∏–±–∫–∞. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <form onSubmit={handleSubmit(onSubmit)} className="flex flex-col h-full justify-between">
            <div>
                <input
                    {...register('name')}
                    placeholder="–í–∞—à–µ –∏–º—è"
                    className={errors.name ? 'border-red-500' : ''}
                    aria-invalid={!!errors.name}
                />
                {errors.name && (
                    <p className="text-red-500 text-sm" role="alert">
                        {errors.name.message}
                    </p>
                )}
            </div>

            <div>
                <input
                    {...register('email')}
                    type="email"
                    placeholder="–í–∞—à Email"
                    className={errors.email ? 'border-red-500' : ''}
                    aria-invalid={!!errors.email}
                />
                {errors.email && (
                    <p className="text-red-500 text-sm" role="alert">
                        {errors.email.message}
                    </p>
                )}
            </div>

            <div>
        <textarea
            {...register('message')}
            placeholder="–í–∞—à–µ —Å–æ–æ–±—â–µ–Ω–∏–µ"
            className={errors.message ? 'border-red-500' : ''}
            aria-invalid={!!errors.message}
        />
                {errors.message && (
                    <p className="text-red-500 text-sm" role="alert">
                        {errors.message.message}
                    </p>
                )}
            </div>

            <button
                type="submit"
                disabled={loading}
                className={loading ? 'opacity-50 cursor-not-allowed' : ''}
                aria-busy={loading}
            >
                {loading ? '–û—Ç–ø—Ä–∞–≤–∫–∞...' : '–û—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ'}
            </button>
        </form>
    );
}
